import { Component, OnInit } from '@angular/core';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { ActivatedRoute } from '@angular/router';
import { Message } from "primeng/primeng";
import { ApplyApproveBean } from './bean/applyApproval.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Cust2List } from '../customIntroduceApply/bean/addCustIntroduceApply.bean';
import { DatePipe } from '@angular/common';
import * as moment from 'moment';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { checkNumber, custNoTen } from 'app/@uisftech/common/service/validator';
@Component({
    selector: 'applyApproval',
    templateUrl: './applyApproval.component.html',
    styleUrls: ['./applyApproval.component.scss'],
    providers: [CusOperationHttpService, Commfunc, DatePipe]
})
export class ApplyApprovalComponent implements OnInit {
    first: number = 0;
    code: any;
    relaStatusOptions: any;//审批状态
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    msgs: Message[] = [];
    ch: any = CALENDAR_CN;
    applyList: {
        introducerId: '',
        introducerName: '',
        fromDate: '',
        thruDate: '',
        introducerAcctId: '',
        relaStatus: '',
        createdUserName: '',
        createdOrgName: '',
    }
    applyApproveBean: ApplyApproveBean = new ApplyApproveBean();
    approveList: any;


    introform: FormGroup;
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    list_one: Cust2List = new Cust2List();
    applyId: string;//
    checkFlag: boolean = false;//校验是否成功
    //单笔校验
    cust2List: any = [];
    //单笔校验
    custList: any = {
        coverCustId: '',
        coverCustName: '',
        remarksInfo: '',
        applyId: '',
        relaId: '',
    };
    arr: any = [];
    pageSizeCover: number = 10;
    pageNumCover: number = 1;
    totalCover: number;
    itemCover: any;
    flag: string;//是否是历史
    operationTypeIn: string;//传入的操作类型
    coverShowFlag: boolean = false;//审批是否可修改
    detailList: any = [];
    inValue: any;//传值
    isShowTip: boolean = true;//校验隐藏
    custIntroduceParam: any;//审批传值
    constructor(
        public httpService: CusOperationHttpService,
        public routeInfo: ActivatedRoute,
        public commfunc: Commfunc,
        public fb: FormBuilder,
        private datePipe: DatePipe,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
    }

    ngOnInit() {
        this.introform = this.fb.group({
            'cust2No': new FormControl('', Validators.required),
            'cust2Name': new FormControl('', Validators.required),
        });
        this.codeValues();
        this.relaStatusOptions = this.code['relaStatusIntro'];//状态


        this.custIntroduceParam = JSON.parse(this.commfunc.getSessionDataCH('custIntroduce'));
        // console.log(this.custIntroduceParam)
        if (this.custIntroduceParam) {
            this.detailList = this.custIntroduceParam;
            this.approveList = JSON.parse(this.custIntroduceParam['dataMap']);
            this.inValue = this.approveList;
            // console.log(this.inValue)
            this.operationTypeIn = this.approveList['operationType'];
            this.applyId = this.approveList['applyId'];
            // this.applyApproveBean = this.approveList.introduceServiceDetailListMap.pageList[0];
            // this.cust2List = this.approveList.introduceServiceDetailListMap.pageList;
            this.query();
            this.flag = this.custIntroduceParam['markFlag'];
            if (this.custIntroduceParam['taskRate'] == '1' && this.flag != '2') {
                this.coverShowFlag = true;
            }
        }
    }
    query() {
        let temp = {
            operationNum: this.approveList['applyId'],
            operationType: this.approveList['operationType'],
            pageSize: this.pageSizeCover,
            pageNum: this.pageNumCover
        }
        this.httpService.introduceServiceDetailList(temp).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.pageList && data.pageList.length > 0) {
                    this.applyApproveBean = data.pageList[0];
                    this.cust2List = data.pageList;
                    this.totalCover = data.totalNum;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '没有数据!' });
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //被介绍客户名称回显
    myFunction2() {
        if (this.list_one.cust2No && this.list_one.cust2No != '') {
            if (this.applyApproveBean.introducerId == this.list_one.cust2No) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '介绍人、被介绍人不能是同一个客户!' }];
                return
            }
            let param = {
                custNo: this.list_one.cust2No
            }
            this.httpService.selectCustBriefInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.list_one.cust2Name = data.custName;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            })
        }
    }
    //校验客户号只能输入数字
    onKeyupNumber(event) {
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字!' }];
                event.target.value = '';
                return
            }
            if (custNoTen(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号错误!' }];
                event.target.value = '';
                return
            }
        } else {
            this.list_one.cust2Name = '';
        }
    }
    //被介绍添加
    add() {
        this.isShowTip = true;
        //校验的
        for (const i in this.introform.controls) {
            this.introform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.introform.controls) {
            if (!this.introform.controls[i].valid) {
                return;
            }
        }
        if (this.applyApproveBean.introducerId && this.applyApproveBean.introducerId != ''
            && this.applyApproveBean.introducerName && this.applyApproveBean.introducerName != ''
            && this.applyApproveBean.introducerAcctId && this.applyApproveBean.introducerAcctId != ''
            && this.applyApproveBean.thruDate && this.applyApproveBean.thruDate != null
            && this.applyApproveBean.fromDate && this.applyApproveBean.fromDate != null) {
            let temp = {
                applyId: this.applyId,
                custId: this.applyApproveBean.introducerId,
                custName: this.applyApproveBean.introducerName,
                custAcctNo: this.applyApproveBean.introducerAcctId,
                fromDate: this.datePipe.transform(this.applyApproveBean.fromDate, 'yyyy-MM-dd'),
                thruDate: this.datePipe.transform(this.applyApproveBean.thruDate, 'yyyy-MM-dd'),
                coverCustId: this.list_one.cust2No,
                coverCustName: this.list_one.cust2Name,
                createLoginName: this.userName,
                createOrgName: this.orgName,
            }
            let flag = false;
            for (let item of this.cust2List) {
                if (item.coverIntroduceId == this.list_one.cust2No) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '被介绍客户号重复!' });
                    return flag = true;
                }
            }
            this.httpService.ckCovIntroService(temp).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.isShowTip = false;
                    this.custList.coverIntroduceId = data.coverCustId;
                    this.custList.coverIntroduceName = data.coverCustName;
                    this.applyId = data.applyId;
                    this.custList.applyId = data.applyId;
                    this.custList.relaId = data.relaId;
                    if (!flag) {
                        let b = JSON.parse(JSON.stringify(this.custList));
                        for (let i of this.cust2List) {
                            this.arr.push(i);
                        }
                        this.arr.push(b);
                        this.cust2List = JSON.parse(JSON.stringify(this.arr));
                        this.arr = [];
                    }
                    this.list_one = new Cust2List();
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    return
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            });
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '介绍人信息不全!' });
            return;
        }

    }
    getIndex(list, item) {
        let index = -1;
        for (let i = 0; i < list.length; i++) {
            if (list[i].coverIntroduceId == item.coverIntroduceId) {
                index = i;
                break;
            }
        }
        return index
    }
    //删除
    delete(car) {
        let carList = JSON.parse(JSON.stringify(this.cust2List));
        let i = this.getIndex(carList, car)
        carList.splice(i, 1);
        this.cust2List = carList;
        this.arr = [];
        let temp = {
            applyId: car.applyId,
            relaIds: [car.relaId],
        }
        this.httpService.introduceServiceDelete(temp).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功!' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    paginateCover(event) {
        //每页显示的条数
        this.pageSizeCover = event.rows * 1;
        //当前页
        this.pageNumCover = event.page + 1;
        this.first = event.page * event.rows;
        let param = {
            operationNum: this.applyApproveBean.applyId,
            operationType: this.operationTypeIn,
            pageSize: this.pageSizeCover,
            pageNum: this.pageNumCover
        }
        this.httpService.introduceServiceDetailList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.cust2List = data.pageList;
                this.totalCover = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    //重置
    chongzhi() {
        this.list_one.cust2No = '';
        this.list_one.cust2Name = '';
    }
}
