import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { QueryCustIntroduceApplyBean } from './bean/queryCustIntroduceApply.bean';
import { Message } from 'primeng/primeng';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { DataTableModule, SharedModule } from 'primeng/primeng';
import { checkNumber, custNoTen } from 'app/@uisftech/common/service/validator';
@Component({
    selector: 'customIntroduceApplyQuery',
    templateUrl: './customIntroduceApplyQuery.component.html',
    styleUrls: ['./customIntroduceApplyQuery.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class CustomIntroduceApplyQueryComponent implements OnInit {
    code: any;
    userId: string = '';//登录人编号
    manageId: any;//主联系人
    shenPiOptions: any;//审批状态
    relaStatusOptions: any;//审批状态
    regisStatuOptions: any;//登记簿状态
    operationTypeOptions: any;//操作类型
    remarkOption: any = [];//审批结果
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    first: number = 0;
    msgs: Message[] = [];
    fromDate: Date = null;
    thruDate: Date = null;
    dataDetail: any = [];//详情表
    totalDetail: number;//详情总条数
    pageSizeDetail: number;//
    pageNumDetail: number;//
    //总页数
    total: string;
    //每页多少条

    date: Date = new Date();
    ch: any = CALENDAR_CN;
    addHeader = '选择字段';
    addPage: boolean = false;
    detailHeader = '详情';
    detailPage: boolean = false;
    detailList: any = {
        applyId: '',
        introducerId: '',
        introducerName: '',
        fromDate: '',
        thruDate: '',
        introducerAcctId: '',
        createdUserName: '',
        createdOrgName: '',
        bankCode: '',
        createdStamp: '',
        lastUpdatedStamp: '',
        relaStatus: '',
    };
    isAll: boolean = false;
    tableData: any = [];
    arrList: any = [];
    data = [];
    itemTable: any = [];//审批表
    detailshow: boolean = false;
    updateshow: boolean = false;
    deleteshow: boolean = false;
    queryCustIntroduceApplyBean: QueryCustIntroduceApplyBean = new QueryCustIntroduceApplyBean();
    pageSizeCover: number = 10;
    pageNumCover: number = 1;
    totalCover: number;
    itemCover: any;
    constructor(
        public router: Router,
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc
    ) {
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
     }

    ngOnInit() {
        this.codeValues();
        this.shenPiOptions = this.code['ShenPi'];
        this.relaStatusOptions = this.code['relaStatusIntro'];//状态
        this.regisStatuOptions = this.code['regisStatuIntro'];//登记簿状态
        this.operationTypeOptions = this.code['operationTypeIntro'];//操作类型
        this.remarkOption = this.code['RemarkOption'];//审批结果
        this.pageSizeDetail = 10;
        this.pageNumDetail = 1;
        this.queryCustIntroduceApplyBean.pageSize = 10;
        this.queryCustIntroduceApplyBean.pageNum = 1;
        let param = {
            businessType: "CustIntroduce",
            funcType: "ApplyQuery",
        }
        this.httpService.getDisplayFields(param).subscribe(data => {
            let str = data.fields;
            let checkThis = true;
            for (let item of str) {
                if (item.isCheck == true) {
                    item.isCheck = true;
                    this.tableData.push(item);
                    checkThis = item.isCheck && checkThis;
                } else if (item.isCheck == false) {
                    item.isCheck = false;
                    checkThis = false;
                }
            }
            this.arrList = str;
            this.isAll = checkThis;
        });
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        });
        this.queryFirst();
    }
    toCustomIntroduceRepealQuery() {
        this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceRepealQuery"])
    }
    toCustomIntroduceRelationshipQuery() {
        this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceRelationshipQuery"])
    }
    toCustomIntroduceApply() {
        this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceApply"])
    }
    //翻页
    paginate(event) {
        //每页显示的条数
        this.queryCustIntroduceApplyBean.pageSize = event.rows * 1;
        //当前页
        this.queryCustIntroduceApplyBean.pageNum = event.page + 1;
        this.first = event.page * event.rows;
        this.query();
    }

    chooseBtn() {
        this.addPage = true;
    }
    //单选
    doSelect(data) {
        let cellThis = true;
        if (data.isCheck) {
            this.tableData.push(data);
            this.arrList.forEach(item => {
                cellThis = item.isCheck && cellThis
            });
        } else {
            this.tableData.forEach((item, i) => {
                if (item.value == data.value) {
                    this.tableData.splice(i, 1);
                    return;
                }
            })
            cellThis = false;
        }
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
        this.isAll = cellThis;
    }
    //全选
    checkAll() {
        let isTrue = this.isAll;
        this.tableData = [];
        this.arrList.forEach(item => {
            item['isCheck'] = isTrue;
            if (isTrue) {
                this.tableData.push(item);
            }
        });
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
    }
    //详情
    toDetail(item) {
        this.itemCover = item;
        this.first = 0;
        let param = {
            operationNum: item.operationNum,
            operationType: '1',
            pageSize: this.pageSizeCover,
            pageNum: this.pageNumCover
        }
        this.httpService.introduceServiceDetailList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.pageList && data.pageList.length > 0) {
                    this.detailList = data.pageList[0];
                    this.dataDetail = data.pageList;
                } else {
                    this.detailList = [];
                    this.dataDetail = [];
                }
                this.totalCover = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
        this.detailPage = true;
    }
    //字段保存
    save() {
        this.addPage = false;
        let arr = [];
        this.tableData.forEach(item => {
            arr.push(item.value)
        });
        let param = {
            businessType: "CustIntroduce",
            funcType: "ApplyQuery",
            fields: arr,
        }
        this.httpService.getDisplayFields(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    //生效日期
    showStart() {
        if (this.fromDate && this.fromDate != null && this.thruDate && this.thruDate != null) {
            if (this.fromDate.getTime() > this.thruDate.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '生效日期必须小于到期日期!' }];
                setTimeout(() => {
                    this.fromDate = null;
                });
                return
            }
        }
    }
    //到期日期
    showEnd() {
        if (this.fromDate && this.fromDate != null && this.thruDate && this.thruDate != null) {
            if (this.fromDate.getTime() > this.thruDate.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '到期日期必须大于生效日期!' }];
                setTimeout(() => {
                    this.thruDate = null;
                });
                return
            }
        }
    }
    queryFirst() {
        this.queryCustIntroduceApplyBean.pageSize = 10;
        this.queryCustIntroduceApplyBean.pageNum = 1;
        this.first = 0;
        this.query();
    }
    //查询
    query() {
        this.queryCustIntroduceApplyBean.relaStatus = "1,2,3,4";
        this.queryCustIntroduceApplyBean.operationType = "1";
        this.queryCustIntroduceApplyBean.fromDate = this.commfunc.transDateN(this.fromDate);
        this.queryCustIntroduceApplyBean.thruDate = this.commfunc.transDateN(this.thruDate);
        this.httpService.introduceServiceList(this.queryCustIntroduceApplyBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.pageList && data.pageList.length > 0) {
                    for (let item of data.pageList) {
                        
                        this.httpService.findUser({ custNo: item.introducerId }).subscribe(dataUser => {
                            if (dataUser.returnCode.code == 'success') {
                                if (dataUser.infoList != null) {
                                    if (dataUser.infoList.length > 0) {
                                        this.manageId = dataUser.infoList[0].manageId;
                                        item['manageId'] = this.manageId;
                                        //console.log( data.pageList);
                                    }
                                }
                            } 
                        })
                        if (item.relaStatus == '1' || item.relaStatus == '2' || item.relaStatus == '3' ||
                            item.relaStatus == '4' || item.relaStatus == '6' ||
                            item.relaStatus == '10') {
                            item.detailshow = true;
                        }
                        if (item.relaStatus == '1' || item.relaStatus == '4' ||
                            item.relaStatus == '10') {
                            item.updateshow = true;
                        }
                        if (item.relaStatus == '1') {
                            item.deleteshow = true;
                        }
                        for (let temp of this.relaStatusOptions) {
                            if (item.relaStatus == temp.value) {
                                item.relaStatus = temp.label
                            }
                        }
                        for (let temp of this.regisStatuOptions) {
                            if (item.regisStatu == temp.value) {
                                item.regisStatu = temp.label
                            }
                        }
                        for (let temp of this.operationTypeOptions) {
                            if (item.operationType == temp.value) {
                                item.operationType = temp.label
                            }
                        }
                    }
                    this.data = data.pageList;
                    this.total = data.totalNum;
                } else {
                    this.data = [];
                    this.total = data.totalNum;
                }
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    chongzhi() {
        this.queryCustIntroduceApplyBean = new QueryCustIntroduceApplyBean();
        this.fromDate = null;
        this.thruDate = null;
        this.queryCustIntroduceApplyBean.pageSize = 10;
        this.queryCustIntroduceApplyBean.pageNum = 1;
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //修改
    toUpdate(data) {
        data['updateFlag'] = '0'
        this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceApply", data])
    }

    //校验客户号只能输入数字
    onKeyupNumber(event) {
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字!' }];
                event.target.value = '';
                return
            }
            if (custNoTen(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号错误!' }];
                event.target.value = '';
                return
            }
        }
    }
    //删除
    toDelete(car) {
        let temp = {
            applyId: car.operationNum,
            relaIds: car.relaIds,
        }
        this.httpService.introduceServiceDelete(temp).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功!' });
                this.queryFirst();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    paginateCover(event) {
        //每页显示的条数
        this.pageSizeCover = event.rows * 1;
        //当前页
        this.pageNumCover = event.page + 1;
        this.first = event.page * event.rows;
        let param = {
            operationNum: this.itemCover.applyId,
            operationType: '1',
            pageSize: this.pageSizeCover,
            pageNum: this.pageNumCover
        }
        this.httpService.introduceServiceDetailList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataDetail = data.pageList;
                this.totalCover = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}
