import { Component, OnInit, ChangeDetectorRef, OnChanges, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { AddCustIntroduceApplyBean, Cust2List, ExTableList, BussinessMap } from './bean/addCustIntroduceApply.bean';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Message, DropdownModule, ConfirmationService } from 'primeng/primeng';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import * as moment from 'moment';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { DatePipe } from '@angular/common';
import { checkNumber, custNoTen } from 'app/@uisftech/common/service/validator';

@Component({
    selector: 'customIntroduceApply',
    templateUrl: './customIntroduceApply.component.html',
    styleUrls: ['./customIntroduceApply.component.scss'],
    providers: [Commfunc, CusOperationHttpService, DatePipe, ConfirmationService]
})
export class CustomIntroduceApplyComponent implements OnInit, OnDestroy {
    code: any;
    first: number = 0;
    relaStatusOptions: any;//审批状态
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    dateEnd: any;
    userform: FormGroup;
    introform: FormGroup;
    msgs: Message[] = [];
    items: any = [];
    fromDate: Date = null;
    thruDate: Date = null;
    ch: any = CALENDAR_CN;
    isChecked: string = '1';
    customcreate: boolean = true;
    bulkImport: boolean = false;
    UPLOAD: string = TZB_CUS_UPLOAD;
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;

    //批量校验
    exTableList: any = {
        coverCustId: '',
        coverCustName: '',
        remarksInfo: '',
        applyId: '',
        relaId: '',
    }
    //单笔校验
    cust2List: any = {
        coverCustId: '',
        coverCustName: '',
        remarksInfo: '',
        applyId: '',
        relaId: '',
    };
    cust2No: any;
    cust2Name: any;
    dataOne: any = [];//单笔录入
    arr: any = [];
    uploadedFiles: any[] = [];
    cusList: any;//客户群列表
    partyClassificationGroupId: any;
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    date: Date = new Date();
    newdate: any;
    cars: any = [];
    //隐藏查询
    qryshow: number = 1;
    qryshowPage: boolean = false;
    //批量导入查询
    qryshowMore: number = 1;
    qryshowPageMore: boolean = false;

    excleTable: any = [];//文件导入失败列表
    excleListSuccess: any = [];//文件导入成功列表
    groupTable: any = [];//客户群失败列表
    groupListSuccess: any = [];//客户群成功列表

    yesOrNo: boolean = false;
    applyId: string = "";//申请批次号

    checkFlag: boolean = false;//校验标志
    coverIntroducerName: string;//被介绍姓名
    coverIntroducerId: string;//被介绍客户号

    coverIntroducerIdEx: string;
    coverIntroducerNameEx: string;
    addCustIntroduceApplyBean: AddCustIntroduceApplyBean = new AddCustIntroduceApplyBean();
    list_one: Cust2List = new Cust2List();
    list_exTable: ExTableList = new ExTableList();
    updateList: any;

    //选人
    choosePerson: boolean = false;
    waitSelUserList: any;
    outVal: any;

    //重新校验
    custNo1: string;
    thruDate1: any;
    custAcctNo1: any;

    firstPage: boolean = true;//第一步
    secondPage: boolean = false;//第二步
    excleFailedTable: any = [];//校验失败的展示列表
    errorPage: boolean = false;//校验失败的展示弹框
    sumNum: number;//总条数
    successNum: number;//成功的条数
    failedNum: number;//失败的条数
    //批量被介绍的分页
    pageSizeCover: number = 10;
    pageNumCover: number = 1;
    totalCover: number;
    isShowTip: boolean = true;//校验隐藏
    temp_No: string;//暂存的客户号
    changePart: number = 0;//上下一步切换
    constructor(
        public fb: FormBuilder,
        public router: Router,
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService,
        public ce: ChangeDetectorRef,
        private datePipe: DatePipe,
        public confirmationService: ConfirmationService,
        public routeInfo: ActivatedRoute,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        this.userform = fb.group({
            custNo: ['', Validators.required],
            custName: ['', Validators.required],
            custAcctNo: ['', Validators.required],
            fromDate: [''],
            thruDate: ['', Validators.required],
        });
        this.introform = this.fb.group({
            'cust2No': new FormControl('', Validators.required),
            'cust2Name': new FormControl('', Validators.required),
        });
    }

    ngOnInit() {
        this.codeValues();
        this.relaStatusOptions = this.code['relaStatusIntro'];//状态
        this.updateList = this.routeInfo.snapshot.params;
        this.fromDate = this.date;
        this.dateEnd = moment().add(1, 'year').format('Y-MM-DD');
        this.thruDate = new Date(this.dateEnd);

        //console.log(this.routeInfo.snapshot.params)
        if (this.updateList['updateFlag'] == '0') {
            let temp = {
                operationType: '1',
                reStas: '1,4',
                relaStatus: 'relaStatus',
                operationNum: this.updateList['operationNum']
            }
            let items = {
                operationNum: this.updateList['operationNum'],
                operationType: '1',
            }
            this.httpService.introduceServiceList(items).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.pageList && data.pageList.length > 0) {
                        let updateArray = [];
                        data.pageList.forEach((item, i) => {
                            if (this.updateList.operationNum == item.operationNum) {
                                updateArray.push(item);
                            }
                        });
                        this.applyId = updateArray[0].operationNum;
                        this.addCustIntroduceApplyBean.custNo = updateArray[0].introducerId;
                        this.addCustIntroduceApplyBean.custName = updateArray[0].introducerName;
                        this.addCustIntroduceApplyBean.custAcctNo = updateArray[0].introducerAcctId;
                        this.fromDate = new Date(updateArray[0].fromDate);
                        this.thruDate = new Date(updateArray[0].thruDate);
                        this.userName = updateArray[0].applicantUserName;
                        this.date = updateArray[0].createdStamp;
                        this.orgName = updateArray[0].applicantOrgName;
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            });
            this.httpService.introduceServiceDetailList(temp).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.pageList && data.pageList.length > 0) {
                        let updateArray = [];
                        data.pageList.forEach((item, i) => {
                            if (this.updateList.operationNum == item.applyId) {
                                updateArray.push(item);
                            }
                        });
                        let arrTable = [];
                        let arr = [];
                        arr.push({
                            coverCustId: updateArray[0].coverIntroduceId,
                            coverCustName: updateArray[0].coverIntroduceName,
                            applyId: updateArray[0].applyId,
                            relaId: updateArray[0].relaId,
                        })
                        arrTable.push({
                            coverIntroduceId: updateArray[0].coverIntroduceId,
                            coverIntroduceName: updateArray[0].coverIntroduceName,
                            applyId: updateArray[0].applyId,
                            relaId: updateArray[0].relaId,
                        })
                        if (updateArray.length > 1) {
                            let newarr = updateArray.slice(1);
                            for (let item of newarr) {
                                if (item.applyId == updateArray[0].applyId) {
                                    arr.push({
                                        coverCustId: item.coverIntroduceId,
                                        coverCustName: item.coverIntroduceName,
                                        applyId: item.applyId,
                                        relaId: item.relaId,
                                    })
                                    arrTable.push({
                                        coverIntroduceId: item.coverIntroduceId,
                                        coverIntroduceName: item.coverIntroduceName,
                                        applyId: item.applyId,
                                        relaId: item.relaId,
                                    })
                                }
                            }
                        }
                        if (data.pageList[0].introducerType == '0') {
                            this.dataOne = arr;
                            this.isChecked = '1';
                            this.customcreate = true;
                            this.bulkImport = false;
                        } else if (data.pageList[0].introducerType == '1') {
                            this.excleTable = arrTable;
                            this.isChecked = '2';
                            this.customcreate = false;
                            this.bulkImport = true;
                        }
                    } else {
                        return
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            });
        }
    }
    //页面销毁之前调用
    ngOnDestroy() {
        if (this.updateList['updateFlag'] == '0') {
            return;
        }
        this.introduceCheck();
        if (this.addCustIntroduceApplyBean.custNo && this.addCustIntroduceApplyBean.custNo != ''
            && this.addCustIntroduceApplyBean.custName && this.addCustIntroduceApplyBean.custName != ''
            && this.addCustIntroduceApplyBean.custAcctNo && this.addCustIntroduceApplyBean.custAcctNo != ''
            && this.thruDate && this.thruDate != null
            && this.fromDate && this.fromDate != null) {
            let temp = {
                applyId: this.applyId,
                custId: this.addCustIntroduceApplyBean.custNo,
                custName: this.addCustIntroduceApplyBean.custName,
                custAcctNo: this.addCustIntroduceApplyBean.custAcctNo,
                fromDate: this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
                thruDate: this.datePipe.transform(this.thruDate, 'yyyy-MM-dd'),
                coverCustId: this.list_one.cust2No,
                coverCustName: this.list_one.cust2Name,
                createLoginName: this.userName,
                createOrgName: this.orgName,
            }
            this.httpService.ckCovIntroService(temp).subscribe(data => {
                if (data.returnCode.code == 'success') {

                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    return
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            });
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请先录入介绍人客户信息!' });
            return;
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //客户录入
    cusCreate() {
        this.customcreate = true;
        this.bulkImport = false;
        this.isChecked = '1';
    }
    //批量导入
    import1() {
        this.isChecked = '2';
        this.customcreate = false;
        this.bulkImport = true;
    }
    //提交审批
    commit() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        if ((!this.dataOne || this.dataOne.length == 0) && (!this.excleTable || this.excleTable.length == 0)) {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '必须选择一种被介绍客户录入方式!' });
            return;
        } else {
            this.confirmationService.confirm({
                message: '是否提交审批?',
                accept: () => {
                    this.submit_1();
                }
            });
        }
    }
    //提交1
    submit_1() {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.waitSelUserList = this.commfunc.query('KHJSKHLC', {});
        if (!this.waitSelUserList.erro) {
            this.outVal = this.waitSelUserList.waitSelUserList;
            let chooseThis = this.waitSelUserList.display;
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            let a2 = commonHeader.extParam.specifyNextOpers;
            if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                this.choosePerson = true;
            } else {
                this.submit_2();
            }
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
        }
    }
    //提交2
    submit_2() {
        let params = {
            applyId: this.applyId,
            operationType: "1",
        }
        this.httpService.approveCustIntroduceCust(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '提交成功' });
                setTimeout(() => {
                    this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceApplyQuery"]);
                }, 2000);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    outDisplayCall(event) {
        this.choosePerson = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.submit_2();
    }
    choosePersonHide() {
        this.choosePerson = false;
    }
    //被介绍添加
    add() {
        if (this.temp_No != this.list_one.cust2No) {
            this.myFunction2();
        }
        this.isShowTip = true;
        //校验的
        for (const i in this.introform.controls) {
            this.introform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.introform.controls) {
            if (!this.introform.controls[i].valid) {
                return;
            }
        }
        if (this.addCustIntroduceApplyBean.custNo && this.addCustIntroduceApplyBean.custNo != ''
            && this.addCustIntroduceApplyBean.custName && this.addCustIntroduceApplyBean.custName != ''
            && this.addCustIntroduceApplyBean.custAcctNo && this.addCustIntroduceApplyBean.custAcctNo != ''
            && this.thruDate && this.thruDate != null
            && this.fromDate && this.fromDate != null) {
            let temp = {
                applyId: this.applyId,
                custId: this.addCustIntroduceApplyBean.custNo,
                custName: this.addCustIntroduceApplyBean.custName,
                custAcctNo: this.addCustIntroduceApplyBean.custAcctNo,
                fromDate: this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
                thruDate: this.datePipe.transform(this.thruDate, 'yyyy-MM-dd'),
                coverCustId: this.list_one.cust2No,
                coverCustName: this.list_one.cust2Name,
                createLoginName: this.userName,
                createOrgName: this.orgName,
            }
            let flag = false;
            for (let item of this.dataOne) {
                if (item.coverCustId == this.list_one.cust2No) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '被介绍客户号重复!' });
                    return flag = true;
                }
            }
            this.httpService.ckCovIntroService(temp).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.isShowTip = false;
                    this.cust2List.remarksInfo = data.remarksInfo;
                    this.cust2List.coverCustId = data.coverCustId;
                    this.cust2List.coverCustName = data.coverCustName;
                    this.applyId = data.applyId;
                    this.cust2List.applyId = data.applyId;
                    this.cust2List.relaId = data.relaId;
                    if (!flag) {
                        let b = JSON.parse(JSON.stringify(this.cust2List));
                        for (let i of this.dataOne) {
                            this.arr.push(i);
                        }
                        this.arr.push(b);
                        this.dataOne = JSON.parse(JSON.stringify(this.arr));
                        this.arr = [];
                    }
                    this.list_one = new Cust2List();
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    return
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            });
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请先录入介绍人客户信息!' });
            return;
        }

    }
    getIndex(list, item) {
        let index = -1;
        for (let i = 0; i < list.length; i++) {
            if (list[i].coverIntroduceId == item.coverIntroduceId) {
                index = i;
                break;
            }
        }
        return index
    }
    getIndex_one(list, item) {
        let index = -1;
        for (let i = 0; i < list.length; i++) {
            if (list[i].coverCustId == item.coverCustId) {
                index = i;
                break;
            }
        }
        return index
    }
    //删除
    delete(car, num) {
        if (num == 1) {
            let carList = JSON.parse(JSON.stringify(this.dataOne));
            let i = this.getIndex_one(carList, car)
            carList.splice(i, 1);
            this.dataOne = carList;
            this.arr = [];
        } else if (num == 2) {
            let carList = JSON.parse(JSON.stringify(this.excleTable));
            let i = this.getIndex(carList, car)
            carList.splice(i, 1);
            this.excleTable = carList;
            this.arr = [];
        }
        let temp = {
            applyId: car.applyId,
            relaIds: [car.relaId],
        }
        this.httpService.introduceServiceDelete(temp).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功!' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    //文件导入
    myUploader(event) {
        if (!this.addCustIntroduceApplyBean.custNo
            || !this.addCustIntroduceApplyBean.custName
            || !this.addCustIntroduceApplyBean.custAcctNo
            || !this.thruDate) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请先录入介绍人客户信息!' });
            return;
        } else {
            if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
                this.uploadedFiles = JSON.parse(event.xhr.response);
                if (this.uploadedFiles.length > 1) {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '不支持上传多个文件！' }];
                    return;
                }
                if (JSON.parse(event.xhr.response).success == false) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: JSON.parse(event.xhr.response).errorMsg });
                    return
                }
            }
            let temp = {
                applyId: this.applyId,
                custId: this.addCustIntroduceApplyBean.custNo,
                custName: this.addCustIntroduceApplyBean.custName,
                custAcctNo: this.addCustIntroduceApplyBean.custAcctNo,
                fromDate: this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
                thruDate: this.datePipe.transform(this.thruDate, 'yyyy-MM-dd'),
                createLoginName: this.userName,
                createOrgName: this.orgName,
                fileName: this.uploadedFiles[0].fileName,
                fileUrl: this.uploadedFiles[0].fileUrl
            }
            this.httpService.ckBatchCovIntroService(temp).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.applyId = data.applyId;
                    this.excleFailedTable = data.ckFailResultList;
                    this.sumNum = data.totalNum;
                    this.successNum = data.successNum;
                    this.failedNum = data.failNum;
                    this.moreCusCheck();
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: '批量导入校验成功!' });
                    this.errorPage = true;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    return;
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
                return;
            });
        }
    }
    //重置
    chongzhi() {
        this.list_one.cust2No = '';
        this.list_one.cust2Name = '';
    }
    //客户号回显客户名称
    myFunction() {
        if (this.addCustIntroduceApplyBean.custNo && this.addCustIntroduceApplyBean.custNo != '') {
            if (this.addCustIntroduceApplyBean.custNo == this.list_one.cust2No) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '介绍人、被介绍人不能是同一个客户!' }];
                return;
            }
            let param = {
                custNo: this.addCustIntroduceApplyBean.custNo
            }
            this.httpService.selectCustBriefInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.addCustIntroduceApplyBean.custName = data.custName;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            })
        }
    }
    //被介绍客户名称回显
    myFunction2() {
        if (this.list_one.cust2No && this.list_one.cust2No != '') {
            if (this.addCustIntroduceApplyBean.custNo == this.list_one.cust2No) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '介绍人、被介绍人不能是同一个客户!' }];
                return
            }
            let param = {
                custNo: this.list_one.cust2No
            }
            this.httpService.selectCustBriefInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.list_one.cust2Name = data.custName;
                    this.temp_No = JSON.parse(JSON.stringify(this.list_one.cust2No));
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    this.temp_No = JSON.parse(JSON.stringify(this.list_one.cust2No));
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                this.temp_No = JSON.parse(JSON.stringify(this.list_one.cust2No));
            })
        } else {
            this.list_one.cust2Name = '';
        }
    }
    //到期日期大于当前，小于T+365
    checkDate() {
        if (this.fromDate && this.thruDate) {
            if (this.thruDate < this.fromDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '到期日期必须大于生效日期!' });
                this.thruDate = null;
                return;
            }
            if (this.datePipe.transform(this.thruDate, 'yyyy-MM-dd') > this.dateEnd) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '到期日期必须小于' + this.dateEnd });
                this.thruDate = null;
                return;
            }
        }
    }
    //隐藏查询条件
    showQry(data) {
        if (data == 1) {
            this.qryshowPage = true;
            this.qryshow = 2;
        } else if (data == 2) {
            this.qryshowPage = false;
            this.qryshow = 1;
        }
    }
    //批量导入查询
    showQryMore(data) {
        if (data == 1) {
            this.qryshowPageMore = true;
            this.qryshowMore = 2;
        } else if (data == 2) {
            this.qryshowPageMore = false;
            this.qryshowMore = 1;
        }
    }
    //介绍人校验
    introduceCheck() {
        this.isShowTip = true;
        if (this.addCustIntroduceApplyBean.custNo && this.addCustIntroduceApplyBean.custNo != ''
            && this.addCustIntroduceApplyBean.custName && this.addCustIntroduceApplyBean.custName != ''
            && this.addCustIntroduceApplyBean.custAcctNo && this.addCustIntroduceApplyBean.custAcctNo != ''
            && this.thruDate && this.thruDate != null
            && this.fromDate && this.fromDate != null) {
            let param = {
                applyId: this.applyId,
                custId: this.addCustIntroduceApplyBean.custNo,
                custName: this.addCustIntroduceApplyBean.custName,
                custAcctNo: this.addCustIntroduceApplyBean.custAcctNo,
                fromDate: this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
                thruDate: this.datePipe.transform(this.thruDate, 'yyyy-MM-dd'),
                createLoginName: this.userName,
                createOrgName: this.orgName,
            }
            this.httpService.ckIntroService(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.isShowTip = false;
                    this.checkFlag = true;
                    this.custNo1 = this.addCustIntroduceApplyBean.custNo;
                    this.custAcctNo1 = this.addCustIntroduceApplyBean.custAcctNo;
                    this.thruDate1 = this.addCustIntroduceApplyBean.thruDate;
                    if (data.applyId && data.applyId != '' && data.applyId != null) {
                        this.applyId = data.applyId;
                    }
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: '贡献积数赠送到有效介绍账号，如为定期账号销户将无法赠送!' });
                    //只有点击上下一步时才切换
                    if (this.checkFlag && this.changePart == 1) {
                        this.firstPage = false;
                        this.secondPage = true;
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //被介绍模糊查询
    queryCover(item) {
        if (item == 1) {
            let temp = {
                operationNum: this.applyId,
                custNo: this.addCustIntroduceApplyBean.custNo,
                coverIntroducerId: this.coverIntroducerId,
                coverIntroducerName: this.coverIntroducerName,
                operationType: '1',
                relaStatus: 'relaStatus'
            }
            this.httpService.introduceServiceDetailList(temp).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    let array = [];
                    if (data.pageList && data.pageList.length > 0) {
                        for (let item of data.pageList) {
                            array.push({
                                coverCustId: item.coverIntroduceId,
                                coverCustName: item.coverIntroduceName,
                                remarksInfo: item.remarksInfo,
                                applyId: item.applyId,
                                relaId: item.relaId,
                                relaStatus: item.relaStatus
                            })
                        }
                        this.dataOne = array;
                    } else {
                        this.dataOne = [];
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        } else if (item == 2) {
            let temp = {
                operationNum: this.applyId,
                custNo: this.addCustIntroduceApplyBean.custNo,
                coverIntroducerId: this.coverIntroducerIdEx,
                coverIntroducerName: this.coverIntroducerNameEx,
                operationType: '1',
                relaStatus: 'relaStatus'
            }
            this.httpService.introduceServiceDetailList(temp).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.pageList && data.pageList.length > 0) {
                        this.excleTable = data.pageList;
                    } else {
                        this.excleTable = [];
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //被介绍客户模糊查询重置
    chongzhiCover(item) {
        if (item == 1) {
            this.coverIntroducerName = '';
            this.coverIntroducerId = '';
        } else if (item == 2) {
            this.coverIntroducerIdEx = '';
            this.coverIntroducerNameEx = '';
        }
    }
    //批量校验回显
    moreCusCheck() {
        let param = {
            operationNum: this.applyId,
            operationType: '1',
            relaStatus: 'relaStatus',
            pageSize: this.pageSizeCover,
            pageNum: this.pageNumCover
        }
        this.httpService.introduceServiceDetailList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let arr = [];
                for (let item of data.pageList) {
                    arr.push({
                        coverIntroduceId: item.coverIntroduceId,
                        coverIntroduceName: item.coverIntroduceName,
                        remarksInfo: item.remarksInfo,
                        applyId: item.applyId,
                        relaId: item.relaId,
                        relaStatus: item.relaStatus
                    })
                }
                this.excleTable = arr;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    paginateCover(event) {
        //每页显示的条数
        this.pageSizeCover = event.rows * 1;
        //当前页
        this.pageNumCover = event.page + 1;
        this.first = event.page * event.rows;
        this.moreCusCheck();
    }
    //校验客户号只能输入数字
    onKeyupNumber(event, num) {
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字!' }];
                event.target.value = '';
                return
            }
            if (custNoTen(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号错误!' }];
                event.target.value = '';
                return
            }
        } else {
            if (num == 1) {
                this.addCustIntroduceApplyBean.custName = '';
            } else if (num == 2) {
                this.list_one.cust2Name = '';
            }
        }
    }
    //上一步下一步
    toNext(num) {
        this.checkFlag = false;
        this.changePart = 1;
        if (num == 1) {
            if (this.addCustIntroduceApplyBean.custNo && this.addCustIntroduceApplyBean.custNo != ''
                && this.addCustIntroduceApplyBean.custName && this.addCustIntroduceApplyBean.custName != ''
                && this.addCustIntroduceApplyBean.custAcctNo && this.addCustIntroduceApplyBean.custAcctNo != ''
                && this.thruDate && this.thruDate != null) {
                this.introduceCheck();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '必输项不能为空!' }];
                return
            }
        } else if (num == 2) {
            this.firstPage = true;
            this.secondPage = false;
        }
    }

    //介绍人重置
    reset() {
        this.addCustIntroduceApplyBean.custNo = '';
        this.addCustIntroduceApplyBean.custName = '';
        this.addCustIntroduceApplyBean.custAcctNo = '';
        this.addCustIntroduceApplyBean.fromDate = '';
        this.addCustIntroduceApplyBean.thruDate = '';
        this.thruDate = null;
        this.fromDate = new Date();
        this.checkFlag = false;
        this.userName = this.user2.userName;
        this.orgName = this.user2.orgName;
        this.date = new Date();
        this.isShowTip = false;
    }
    //关闭错误弹框
    closeBack() {
        this.errorPage = false;
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}
