import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { QueryCustIntroduceRelaBean } from './bean/queryCustIntroduceRela.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { checkNumber, custNoTen } from 'app/@uisftech/common/service/validator';
@Component({
    selector: 'customIntroduceRelationshipQuery',
    templateUrl: './customIntroduceRelationshipQuery.component.html',
    styleUrls: ['./customIntroduceRelationshipQuery.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class CustomIntroduceRelationshipQueryComponent implements OnInit {
    manageId: any;//主联系人
    userId: any;//登录人
    code: any;
    shenPiOptions: any;//审批状态
    relaStatusOptions: any;//审批状态
    regisStatuOptions: any;//登记簿状态
    operationTypeOptions: any;//操作类型
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    msgs: Message[] = [];
    first: number = 0;
    fromDate: Date = null;
    thruDate: Date = null;
    //总页数
    total: string;
    //每页多少条
    pageSize: string;
    addHeader = '选择字段';
    addPage: boolean = false;
    detailHeader = '详情';
    detailPage: boolean = false;
    date: Date = new Date();
    ch: any = CALENDAR_CN;
    isAll: boolean = false;
    datas: any = [];
    detailList: any = {
        relaId: '',
        custNo: '',
        custName: '',
        cust2No: '',
        cust2Name: '',
        fromDate: '',
        thruDate: '',
        custAcctNo: '',
        relaStatus: '',
        createdDate: '',
        userId: '',
        orgId: '',
    };
    titleList: any = [];
    tableData: any = [];
    showThis: boolean = true;
    queryCustIntroduceRelaBean: QueryCustIntroduceRelaBean = new QueryCustIntroduceRelaBean();

    backList: any;//撤销
    //选人
    choosePerson: boolean = false;
    waitSelUserList: any;
    outVal: any;
    constructor(
        public router: Router,
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService,
        public confirmationService: ConfirmationService
    ) {
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        console.log( this.userId);
    }
    ngOnInit() {
        this.codeValues();
        this.shenPiOptions = this.code['ShenPi'];
        this.relaStatusOptions = this.code['relaStatusIntro'];//状态
        this.regisStatuOptions = this.code['regisStatuIntro'];//登记簿状态
        this.operationTypeOptions = this.code['operationTypeIntro'];//操作类型
        this.queryCustIntroduceRelaBean.pageSize = 10;
        this.queryCustIntroduceRelaBean.pageNum = 1;
        let param = {
            businessType: "CustIntroduce",
            funcType: "DetailQuery",
        }
        this.httpService.getDisplayFields(param).subscribe(data => {
            let str = data.fields;
            let checkThis = true;
            for (let item of str) {
                if (item.isCheck == true) {
                    item.isCheck = true;
                    this.tableData.push(item);
                    checkThis = item.isCheck && checkThis;
                } else if (item.isCheck == false) {
                    item.isCheck = false;
                    checkThis = false;
                }
            }
            this.titleList = str;
            this.isAll = checkThis;
        });
        this.tableData.sort((a, b) => {
            return a.num > b.num
        });
        this.queryFirst();
    }
    toCustomIntroduceApplyQuery() {
        this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceApplyQuery"])
    }
    toCustomIntroduceRepealQuery() {
        this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceRepealQuery"])
    }
    toCustomIntroduceApply() {
        this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceApply"])
    }
    chooseBtn() {
        this.addPage = true;
    }
    //单选
    doSelect(data) {
        let sb = true;
        if (data.isCheck) {
            this.tableData.push(data);
            this.titleList.forEach(item => {
                sb = item.isCheck && sb
            });
        } else {
            this.tableData.forEach((item, i) => {
                if (item.value == data.value) {
                    this.tableData.splice(i, 1);
                    return;
                }
            })
            sb = false;
        }
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
        this.isAll = sb;
    }
    //全选
    checkAll() {
        let isTrue = this.isAll;
        this.tableData = [];
        this.titleList.forEach(item => {
            item['isCheck'] = isTrue;
            if (isTrue) {
                this.tableData.push(item);
            }
        });
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
    }
    save() {
        this.addPage = false;
        let arr = [];
        this.tableData.forEach(item => {
            arr.push(item.value)
        });
        let param = {
            businessType: "CustIntroduce",
            funcType: "DetailQuery",
            fields: arr,
        }
        this.httpService.getDisplayFields(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    //生效日期
    showStart() {
        if (this.fromDate && this.fromDate != null && this.thruDate && this.thruDate != null) {
            if (this.fromDate.getTime() > this.thruDate.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '生效日期必须小于到期日期!' }];
                setTimeout(() => {
                    this.fromDate = null;
                });
                return
            }
        }
    }
    //到期日期
    showEnd() {
        if (this.fromDate && this.fromDate != null && this.thruDate && this.thruDate != null) {
            if (this.fromDate.getTime() > this.thruDate.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '到期日期必须大于生效日期!' }];
                setTimeout(() => {
                    this.thruDate = null;
                });
                return
            }
        }
    }
    queryFirst() {
        this.queryCustIntroduceRelaBean.pageSize = 10;
        this.queryCustIntroduceRelaBean.pageNum = 1;
        this.first = 0;
        this.query();
    }
    query() {
        this.queryCustIntroduceRelaBean.relaStatus = "3,5,6,9,10";
        this.queryCustIntroduceRelaBean.operationType = "1";
        this.queryCustIntroduceRelaBean.fromDate = this.commfunc.transDateN(this.fromDate);
        this.queryCustIntroduceRelaBean.thruDate = this.commfunc.transDateN(this.thruDate);
        this.httpService.query4CustIntrouceRelationList(this.queryCustIntroduceRelaBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.pageList && data.pageList.length > 0) {
                    for (let item of data.pageList) {
                        this.httpService.findUser({ custNo: item.introducerId }).subscribe(dataUser => {
                            if (dataUser.returnCode.code == 'success') {
                                if (dataUser.infoList != null) {
                                    if (dataUser.infoList.length > 0) {
                                        this.manageId = dataUser.infoList[0].manageId;
                                        item['manageId'] = this.manageId;
                                        //console.log( data.pageList);
                                    }
                                }
                            } 
                        })
                        if (item.regisStatu == '1' || item.regisStatu == '3') {
                            item.showThis = true;
                        } else {
                            item.showThis = false;
                        }
                        if (item.relaStatus == '9' || item.relaStatus == '6') {
                            item.regisStatu = '失效';
                            item.showThis = false;
                        }
                        if (item.relaStatus == '5') {
                            item.showThis = false;
                        }
                        for (let temp of this.relaStatusOptions) {
                            if (item.relaStatus == temp.value) {
                                item.relaStatus = temp.label
                            }
                        }
                        for (let temp of this.regisStatuOptions) {
                            if (item.regisStatu == temp.value) {
                                item.regisStatu = temp.label
                            }
                        }
                        for (let temp of this.operationTypeOptions) {
                            if (item.operationType == temp.value) {
                                item.operationType = temp.label
                            }
                        }
                    }
                    this.datas = data.pageList;
                    this.total = data.totalNum;
                } else {
                    this.datas = [];
                    this.total = data.totalNum;
                }
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    paginate(event) {
        //每页显示的条数
        this.queryCustIntroduceRelaBean.pageSize = event.rows * 1;
        //当前页
        this.queryCustIntroduceRelaBean.pageNum = event.page + 1;
        this.first = event.page * event.rows;
        this.query();
    }
    chongzhi() {
        this.queryCustIntroduceRelaBean.custNo = '';
        this.queryCustIntroduceRelaBean.introducerName = '';
        this.queryCustIntroduceRelaBean.coverIntroducerId = '';
        this.queryCustIntroduceRelaBean.coverIntroducerName = '';
        this.queryCustIntroduceRelaBean.fromDate = '';
        this.queryCustIntroduceRelaBean.thruDate = '';
        this.fromDate = null;
        this.thruDate = null;
    }
    //撤销
    toBack(data) {
        this.confirmationService.confirm({
            message: '是否提交撤销审批?',
            accept: () => {
                this.backList = data;
                this.submit_1();
            }
        });
    }
    //提交1
    submit_1() {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.waitSelUserList = this.commfunc.query('KHJSKHLC', {});
        this.outVal = this.waitSelUserList.waitSelUserList;
        let chooseThis = this.waitSelUserList.display;
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let a2 = commonHeader.extParam.specifyNextOpers;
        if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
            this.choosePerson = true;
        } else {
            this.submit_2();
        }
    }
    submit_2() {
        let params = {
            applyId: this.backList.applyId,
            operationType: '2',
            relaId: this.backList.relaId,
            introducerId: this.backList.introducerId,
            introducerName: this.backList.introducerName,
            applicantUserId: this.backList.createdUserId,
        }
        this.httpService.approveCustIntroduceCust(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '提交成功' });
                this.queryFirst();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    outDisplayCall(event) {
        this.choosePerson = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.submit_2();
    }
    choosePersonHide() {
        this.choosePerson = false;
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //校验客户号只能输入数字
    onKeyupNumber(event) {
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字!' }];
                event.target.value = '';
                return
            }
            if (custNoTen(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号错误!' }];
                event.target.value = '';
                return
            }
        }
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}