
import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomIntroduceComponent } from './customIntroduce.component';
import { CustomIntroduceApplyComponent } from './customIntroduceApply/customIntroduceApply.component';
import { CustomIntroduceRepealComponent } from './customIntroduceRepeal/customIntroduceRepeal.component';
import { CustomIntroduceApplyQueryComponent } from './customIntroduceApplyQuery/customIntroduceApplyQuery.component';
import { CustomIntroduceRepealQueryComponent } from './customIntroduceRepealQuery/customIntroduceRepealQuery.component';
import { CustomIntroduceRelationshipQueryComponent } from './customIntroduceRelationshipQuery/customIntroduceRelationshipQuery.component';
import { ApplyApprovalComponent } from './applyApproval/applyApproval.component';
import { RepealApprovalComponent } from './repealApproval/repealApproval.component';




const routes: Routes = [
    {
        path: '',
        component: CustomIntroduceComponent,
        children: [
            {
                path: 'customIntroduceApply',
                component: CustomIntroduceApplyComponent
            },
            {
                path: 'customIntroduceRepeal',
                component: CustomIntroduceRepealComponent
            },
            {
                path: 'customIntroduceApplyQuery',
                component: CustomIntroduceApplyQueryComponent
            },
            {
                path: 'customIntroduceRepealQuery',
                component: CustomIntroduceRepealQueryComponent
            },
            {
                path: 'customIntroduceRelationshipQuery',
                component: CustomIntroduceRelationshipQueryComponent
            },
        ]
    },
    {
        path: 'applyApproval',
        component: ApplyApprovalComponent
    },
    {
        path: 'repealApproval',
        component: RepealApprovalComponent
    }

];
export const CustomIntroduceRouting: ModuleWithProviders = RouterModule.forChild(routes);
