import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { QueryCancelCustIntroduceApplyBean } from './bean/queryCancelCustIntroduceApply.bean';
import { Message } from 'primeng/primeng';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { checkNumber, custNoTen } from 'app/@uisftech/common/service/validator';
@Component({
    selector: 'customIntroduceRepealQuery',
    templateUrl: './customIntroduceRepealQuery.component.html',
    styleUrls: ['./customIntroduceRepealQuery.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class CustomIntroduceRepealQueryComponent implements OnInit {
    code: any;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    shenPiOptions: any;//审批状态
    relaStatusOptions: any;//审批状态
    regisStatuOptions: any;//登记簿状态
    operationTypeOptions: any;//操作类型
    first: number = 0;
    totalDetail: number;//详情总条数
    msgs: Message[] = [];
    fromDate: Date = null;
    thruDate: Date = null;
    //总页数
    total: string;
    //每页多少条
    pageSizeCover: number = 10;
    pageNumCover: number = 1;
    totalCover: number;
    itemCover: any;
    date: Date = new Date();
    ch: any = CALENDAR_CN;
    addHeader = '选择字段';
    addPage: boolean = false;
    detailHeader = '详情';
    detailPage: boolean = false;
    isAll: boolean = false;
    tableData: any = [];
    arrList: any = [];
    data = [];
    detailList: any = {
        introducerId: '',
        introducerName: '',
        introducerAcctId: '',
        fromDate: '',
        thruDate: '',
        createdUserName: '',
        createdStamp: '',
        lastUpdatedStamp: '',
        bankCode: '',
        regisStatu: '',
    };
    itemTable: any = [];//审批表
    dataDetail: any = [
        {
            relaId: '',
            custNo: '',
            custName: '',
            cust2No: '',
            cust2Name: '',

        }
    ];//详情表
    queryCancelCustIntroduceApplyBean: QueryCancelCustIntroduceApplyBean = new QueryCancelCustIntroduceApplyBean();
    constructor(
        public router: Router,
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
    ) {

    }

    ngOnInit() {
        this.codeValues();
        this.shenPiOptions = this.code['ShenPi'];
        this.relaStatusOptions = this.code['introRepealStatus'];//撤销专用状态
        this.regisStatuOptions = this.code['regisStatuIntro'];//登记簿状态
        this.operationTypeOptions = this.code['operationTypeIntro'];//操作类型
        this.queryCancelCustIntroduceApplyBean.pageSize = 10;
        this.queryCancelCustIntroduceApplyBean.pageNum = 1;
        let param = {
            businessType: "CustIntroduce",
            funcType: "RepealDetailQuery",
        }
        this.httpService.getDisplayFields(param).subscribe(data => {
            let str = data.fields;
            let checkThis = true;
            for (let item of str) {
                if (item.isCheck == true) {
                    item.isCheck = true;
                    this.tableData.push(item);
                    checkThis = item.isCheck && checkThis;
                } else if (item.isCheck == false) {
                    item.isCheck = false;
                    checkThis = false;
                }
            }
            this.arrList = str;
            this.isAll = checkThis;
        });
        this.tableData.sort((a, b) => {
            return a.num > b.num
        });
        this.queryFirst();
    }
    toCustomIntroduceApplyQuery() {
        this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceApplyQuery"])
    }
    toCustomIntroduceRelationshipQuery() {
        this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceRelationshipQuery"])
    }
    toCustomIntroduceApply() {
        this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceApply"])
    }
    chooseBtn() {
        this.addPage = true;
    }
    //生效日期
    showStart() {
        if (this.fromDate && this.fromDate != null && this.thruDate && this.thruDate != null) {
            if (this.fromDate.getTime() > this.thruDate.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '生效日期必须小于到期日期!' }];
                setTimeout(() => {
                    this.fromDate = null;
                });
                return
            }
        }
    }
    //到期日期
    showEnd() {
        if (this.fromDate && this.fromDate != null && this.thruDate && this.thruDate != null) {
            if (this.fromDate.getTime() > this.thruDate.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '到期日期必须大于生效日期!' }];
                setTimeout(() => {
                    this.thruDate = null;
                });
                return
            }
        }
    }
    queryFirst() {
        this.queryCancelCustIntroduceApplyBean.pageSize = 10;
        this.queryCancelCustIntroduceApplyBean.pageNum = 1;
        this.first = 0;
        this.query();
    }
    //翻页
    paginate(event) {
        //每页显示的条数
        this.queryCancelCustIntroduceApplyBean.pageSize = event.rows * 1;
        //当前页
        this.queryCancelCustIntroduceApplyBean.pageNum = event.page + 1;
        this.first = event.page * event.rows;
        this.query();
    }
    //单选
    doSelect(data) {
        let sb = true;
        if (data.isCheck) {
            this.tableData.push(data);
            this.arrList.forEach(item => {
                sb = item.isCheck && sb
            });
        } else {
            this.tableData.forEach((item, i) => {
                if (item.value == data.value) {
                    this.tableData.splice(i, 1);
                    return;
                }
            })
            sb = false;
        }
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
        this.isAll = sb;
    }
    //全选
    checkAll() {
        let isTrue = this.isAll;
        this.tableData = [];
        this.arrList.forEach(item => {
            item['isCheck'] = isTrue;
            if (isTrue) {
                this.tableData.push(item);
            }
        });
        this.tableData.sort((a, b) => {
            return a.num * 1 - b.num * 1
        })
    }
    //详情
    toDetail(item) {
        let temp2 = '';
        for (let data_item of this.relaStatusOptions) {
            if (item.relaStatus == data_item.label) {
                temp2 = data_item.value
            }
        }
        this.itemCover = item;
        this.first = 0;
        let temp = '';
        if (item.relaStatus == '5' || item.relaStatus == '撤销审批中'
            || item.relaStatus == '3' || item.relaStatus == '撤销审批未通过') {
            temp = '1';
        } else {
            temp = '2';
        }
        let param = {
            operationNum: item.applyId,
            operationType: temp,
            pageNum: this.pageNumCover,
            pageSize: this.pageSizeCover,
            relaStatus: temp2
        }
        this.httpService.introduceServiceDetailList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.pageList && data.pageList.length > 0) {
                    this.detailList = data.pageList[0];
                    this.dataDetail = data.pageList;
                } else {
                    this.detailList = [];
                    this.dataDetail = [];
                }
                this.totalCover = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
        this.detailPage = true;
    }
    save() {
        this.addPage = false;
        let arr = [];
        this.tableData.forEach(item => {
            arr.push(item.value)
        });
        let param = {
            businessType: "CustIntroduce",
            funcType: "RepealDetailQuery",
            fields: arr,
        }
        this.httpService.getDisplayFields(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    query() {
        this.queryCancelCustIntroduceApplyBean.operationType = "2";
        this.queryCancelCustIntroduceApplyBean.fromDate = this.commfunc.transDateN(this.fromDate);
        this.queryCancelCustIntroduceApplyBean.thruDate = this.commfunc.transDateN(this.thruDate);
        this.httpService.introduceServiceDetailList(this.queryCancelCustIntroduceApplyBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.pageList && data.pageList.length > 0) {
                    for (let item of data.pageList) {
                        for (let temp of this.relaStatusOptions) {
                            if (item.relaStatus == temp.value) {
                                item.relaStatus = temp.label
                            }
                        }
                        for (let temp of this.regisStatuOptions) {
                            if (item.regisStatu == temp.value) {
                                item.regisStatu = temp.label
                            }
                        }
                        for (let temp of this.operationTypeOptions) {
                            if (item.operationType == temp.value) {
                                item.operationType = temp.label
                            }
                        }
                    }
                    this.data = data.pageList;
                    this.total = data.totalNum;
                } else {
                    this.data = [];
                    this.total = data.totalNum;
                }
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    chongzhi() {
        this.queryCancelCustIntroduceApplyBean.custNo = '';
        this.queryCancelCustIntroduceApplyBean.introducerName = '';
        this.queryCancelCustIntroduceApplyBean.coverIntroducerId = '';
        this.queryCancelCustIntroduceApplyBean.coverIntroducerName = '';
        this.queryCancelCustIntroduceApplyBean.fromDate = '';
        this.queryCancelCustIntroduceApplyBean.thruDate = '';
        this.fromDate = null;
        this.thruDate = null;
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    //校验客户号只能输入数字
    onKeyupNumber(event) {
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字!' }];
                event.target.value = '';
                return
            }
            if (custNoTen(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号错误!' }];
                event.target.value = '';
                return
            }
        }
    }
    paginateCover(event) {
        //每页显示的条数
        this.pageSizeCover = event.rows * 1;
        //当前页
        this.pageNumCover = event.page + 1;
        this.first = event.page * event.rows;
        let param = {
            operationNum: this.itemCover.applyId,
            operationType: '2',
            pageSize: this.pageSizeCover,
            pageNum: this.pageNumCover
        }
        this.httpService.introduceServiceDetailList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataDetail = data.pageList;
                this.totalCover = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}
