import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CancelCustIntroduceApplyBean } from './bean/cancelCustIntroduceApply.bean';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
@Component({
    selector: 'customIntroduceRepeal',
    templateUrl: './customIntroduceRepeal.component.html',
    styleUrls: ['./customIntroduceRepeal.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class CustomIntroduceRepealComponent implements OnInit {
    msgs: Message[] = [];
    userform: FormGroup;
    date: Date = new Date();
    ch: any = CALENDAR_CN;
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    custNo: any;
    custName: any;
    custAcctNo: any;
    cust2No: any;
    cust2Name: any;
    fromDate: any;
    thruDate: any;
    cancelCustIntroduceApplyBean: CancelCustIntroduceApplyBean = new CancelCustIntroduceApplyBean();
    constructor(
        public router: Router,
        public fb: FormBuilder,
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        this.userform = fb.group({
            relaId: ['', Validators.required],
            custNo: ['', Validators.required],
            custName: ['', Validators.required],
            custAcctNo: ['', Validators.required],
            cust2No: ['', Validators.required],
            cust2Name: ['', Validators.required],
            fromDate: ['', Validators.required],
            thruDate: ['', Validators.required],
        })

    }

    ngOnInit() {
    }
    toCustomIntroduceApply() {
        this.router.navigate(["pages/tzb/custom/work-rask/customIntroduce/customIntroduceApply"])
    }
    myFunction() {
        let param = {
            relaId: this.cancelCustIntroduceApplyBean.relaId
        }
        this.httpService.getCustIntroduceInfoFromRelaId(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.custNo = data.custNo;
                this.custName = data.custName;
                this.custAcctNo = data.custAcctNo;
                this.cust2No = data.cust2No;
                this.cust2Name = data.cust2Name;
                this.fromDate = new Date(data.fromDate);
                this.thruDate = new Date(data.thruDate);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    commit() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        this.confirmationService.confirm({
            message: '请确认该文件提交?',
            accept: () => {
                this.httpService.cancelCustIntroduceApply(this.cancelCustIntroduceApplyBean).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: '提交成功' });
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
                })
            }
        });
    }
}
